      .h1 Atencin en salud por cada momento de curso de vida

    p.mb-5 De acuerdo con cada momento de vida se brinda una atencin en salud determinada como se puede observar en cada uno de los ciclos de vida o etapas de desarrollo.

    #t_2_1.titulo-segundo
      .h2 2.1 Primera infancia

    p.mb-5 #[strong Poblacin sujeta:] nios y nias desde los 8 das hasta los 5 aos, 11 meses y 29 das. <br> La valoracin integral en salud y el seguimiento del desarrollo integral es un proceso fundamental durante este momento del curso de vida, teniendo en cuenta que la primera infancia es la etapa en la que se establecen las bases para el desarrollo cognitivo, emocional y social del ser humano. As mismo, el desarrollo se expresa de manera particular en cada uno y la interaccin con una amplia variedad de actores, contextos y condiciones permite la potenciacin de las diversas capacidades de las nias y los nios.

    figure.mb-5
      img(src='@/assets/curso/tema2/img1.svg', alt='poblacin sujeta')

    p.mb-4 La valoracin integral de la salud en la primera infancia debe ser realizada de forma complementaria entre los profesionales de medicina general, especialista en medicina familiar o especialista en pediatra y profesional de enfermera, cumpliendo con el esquema de intervenciones/atenciones en salud individuales, definido en el lineamiento tcnico y operativo de la RPMS. En el caso de los nios de mayor riesgo, la valoracin integral debe ser realizada por profesional de pediatra, o por profesionales de medicina general o de medicina familiar, bajo el acompaamiento o la supervisin de pediatra, exclusivamente en los casos en los que no sea posible contar con pediatra.
    p.mb-5 Se consideran nios con mayor riesgo aquellos con presencia de uno o ms de los siguientes factores o antecedentes:

