    .h4.mb-4 Anamnesis primera infancia
    p.mb-5 Durante el procedimiento se debe realizar una anamnesis completa que incluya indagacin de antecedentes y revisin por sistemas. Se recomienda hacer nfasis en:

    .row.justify-content-center.mb-4
      .col-lg-10.alig-self-center
        PasosA.color-acento-contenido.mb-5(tipo='n')
          .row
            .col-md-11.mb-4.mb-md-0
              p Antecedentes personales, mdicos, hospitalarios, transfusionales, farmacolgicos, exposicin al humo de tabaco, alrgicos, quirrgicos, vacunacin, comportamiento general y relaciones interpersonales.

          .row
            .col-md-11.mb-4.mb-md-0
              p Antecedentes familiares (enfermedades, violencias, trastornos de la salud mental y/o consumo de alcohol o SPA en padres, hermanos u otros familiares).

          .row
            .col-md-11.mb-4.mb-md-0
              p Desarrollo: indagar sobre el progreso en los hitos del desarrollo del nio, sobre la adaptacin e integracin en los entornos de educacin inicial; as como preocupaciones de los padres o cuidadores respecto al desarrollo de los nios. Prcticas de crianza y cuidado (comunicacin con el nio o nia y expresin de afecto, actividades para favorecer el desarrollo y juego, exposicin a pantallas, establecimiento de lmites, vivencia de violencia en el hogar, prevencin de accidentes incluyendo seguridad vial).

          .row
            .col-md-11.mb-4.mb-md-0
              p Tamizaje: verificar la realizacin y los resultados de las pruebas de tamizaje neonatal (auditivo por tecnologa dura  potenciales evocados auditivos / otoemisiones acsticas; tamizaje de errores innatos del metabolismo por muestra de sangre; tamizaje de cardiopata congnita compleja y tamizaje visual por examen fsico).

          .row
            .col-md-11.mb-4.mb-md-0
              p Alimentacin en el nio menor de 6 meses haciendo nfasis en la lactancia materna (indagar lactancia materna no exclusiva - averiguar si el nio ha recibido cualquier lquido, alimento o leche diferente a la materna el da o noche anterior). Alimentacin del nio mayor de 6 meses, indagar sobre consumo y hbitos alimentarios en el nio mayor de seis meses indicando tipo de alimentos, frecuencia de consumo, cantidad, forma de preparacin y texturas, adicin de sal y azcar y tipo de alimentos, para lo cual puede ser til la indagacin de la alimentacin en las ltimas 24 horas. A las nias y los nios con #[strong factores de riesgo,] se les debe ordenar una prueba de hemoglobina y segn el resultado derivar a la Ruta Integral de Atencin en Salud para la poblacin con riesgo o presencia de alteraciones nutricionales.

          .row
            .col-md-11.mb-4.mb-md-0
              p Establecimiento de rutinas y hbitos saludables (sueo y descanso, juego o movimiento activo, bao y cambio de paal, cuidado bucal, foto proteccin, control de esfnteres).

          .row
            .col-md-11.mb-4.mb-md-0
              p Valoracin o actualizacin de la conformacin y dinmica de la familia a travs del Familiograma, indagar sobre las capacidades y recursos personales y familiares que inciden en el cuidado de la salud familiar, evaluar el grado de satisfaccin de la funcionalidad familiar, a travs del #[strong APGAR familiar.]

          .row
            .col-md-11.mb-4.mb-md-0
              p Valoracin o actualizacin de las condiciones de vida, del contexto social y las redes de apoyo social y comunitarias de la familia, pertenencia social y cultural que considere pertenencia tnica a travs de #[strong Ecomapa.]

          .row
            .col-md-11.mb-4.mb-md-0
              p Avance en los compromisos acordados en sesiones de educacin individual previas. Inquietudes de los padres.

    .h4.mb-4 Examen fsico completo en la primera infancia 
    p.mb-4 Ante cualquier alteracin encontrada en la anamnesis, en el examen fsico complementado con los instrumentos usados durante la valoracin, debe canalizarse a la ruta integral de atencin en salud para poblacin con riesgo o a la atencin resolutiva por profesional especializado segn sea el caso, para el manejo respectivo. tabla de percentiles de tensin arterial en nios y nias

