              p.mb-2 La tensin arterial debe tomarse a todos los nios mayores de 3 aos de edad o a los menores de esta edad, si tienen sntomas recurrentes de cualquier tipo o antecedente de cateterizacin umbilical en el perodo neonatal. Revisar referencia de tensin arterial por edad. Esta debe hacerse con el manguito adecuado y debe establecerse su normalidad a travs del uso de la tabla de percentiles de tensin arterial en nios y nias. Para ampliar informacin:
