              .h3 Valoracin del desarrollo
              p.mb-2 Se evaluar el desarrollo de los nios en primera infancia a travs de la percepcin de los padres, cuidadores o maestros sobre el desarrollo del nio, as como a travs de la aplicacin directa de la #[strong Escala Abreviada de Desarrollo-3,] ante la sospecha de algn trastorno del desarrollo, se aplicar tambin el test #[strong M-Chat R/F] el cual es un instrumento til para tamizaje   del trastorno del  espectro autista  de manera especfica a todos los nios entre los 18 y los 30 meses de edad.

    .row.justify-content-center
