              .h3 Valoracin del estado nutricional y seguimiento a los parmetros antropomtricos
              p.mb-2 Se realizar tomando las medidas de peso, talla y permetro ceflico, para el clculo de los ndices relacionados con los mismos (IMC/edad, talla/edad, peso/edad y permetro ceflico). La clasificacin antropomtrica se realizar con base en lo establecido en la normatividad vigente y la informacin debe ser registrada en las grficas de los patrones de crecimiento OMS para nias, nios y adolescentes menores de 18 aos.

    .row.justify-content-center
