              .h3 Valoracin de la salud sexual
              p.mb-2 Se debe prestar especial atencin a la deteccin de signos de violencia fsica y sexual, as como anomalas congnitas.

    .row.justify-content-center
      .col-lg-12.align-self-center
        .tarjeta-avatar-b.mb-4
          img(src='@/assets/curso/tema2/img10.svg' alt='AvatarTop')
          .tarjeta.tarjeta--azul
            .p-4
              .h3 Valoracin de la salud visual
              p.mb-2 A partir del examen oftalmoscpico se debe explorar y evaluar las estructuras, se debe valorar la agudeza visual a partir de los tres (3) aos de edad usando optotipos morfoscpicos o angulares.

    .row.justify-content-center
      .col-lg-12.align-self-center
        .tarjeta-avatar-b.mb-4
          img(src='@/assets/curso/tema2/img11.svg' alt='AvatarTop')
          .tarjeta.tarjeta--azul
            .p-4
              .h3 Valoracin de la salud auditiva y comunicativa
              p.mb-2 Incluye la evaluacin del estado del odo y su integridad a travs de la inspeccin visual, la otoscopia, y de las funciones de la articulacin, voz, habla y desempeo comunicativo. Se recomienda aplicar la #[strong lista de chequeo de factores de riesgo de las enfermedades del odo, alteraciones auditivas, vestibulares y de la comunicacin.] Durante la consulta se debe aplicar el cuestionario #[strong VALE].  Verificar resultado de tamizaje auditivo, especialmente nios y nias con riesgo de hipoacusia.

    .row.justify-content-center
      .col-lg-12.align-self-center
        .tarjeta-avatar-b.mb-4
          img(src='@/assets/curso/tema2/img12.svg' alt='AvatarTop')
          .tarjeta.tarjeta--azul
            .p-4
              .h3 Valoracin de la salud bucal
              p.mb-2 Dirigida a realizar el reconocimiento del estado de las estructuras dentomaxilofaciales y de su funcionalidad.

    .row.justify-content-center
      .col-lg-12.align-self-center
        .tarjeta-avatar-b.mb-5
          img(src='@/assets/curso/tema2/img13.svg' alt='AvatarTop')
          .tarjeta.tarjeta--azul
            .p-4
              .h3 Valoracin de la salud mental
              p.mb-2 En este marco es importante indagar acerca de la calidad de la relacin de los padres hacia los hijos, dado que este factor es importante como protector o riesgo de posibles problemas y /o trastornos mentales.  Identificar los hallazgos a partir de la anamnesis, se debe valorar la presencia de riesgos psicosociales para violencias y de exposicin a violencias, exposicin a lesiones en los nios o nias por consumo de sustancias psicoactivas del padre o madre, o algn factor de riesgo como trastorno mental, convulsiones o ausencias, situaciones o momentos significativos (duelo o prdidas), alteraciones o anormalidades relacionadas con la salud mental o neurolgica. Las alteraciones de la salud mental en la primera infancia tienen su expresin principal en los procesos del desarrollo; aunque pueden presentarse alteraciones en el apego, manifestaciones iniciales de trastornos del espectro autista, ansiedad, depresin, entre otras. Debe considerarse la valoracin del riesgo psicosocial del cuidador como factor clave.

