        LineaTiempoD.color-acento-contenido

          p(numero="S" titulo="Servicios sociales y atenciones a la familia") Atenciones complementarias de educacin, cultura, sector social  prosperidad social, entre otras. Verificar escolaridad o escenarios de cuidado para los nios y las nias. Definir atenciones a la familia.

          p(numero="E" titulo="Enfermedad diagnstico, tratamiento y derivacin.") Si de acuerdo con los hallazgos el mdico identifica una patologa, signo o sntoma que no ha sido diagnosticado anteriormente, en esta consulta se podr generar el diagnstico y establecer el manejo o tratamiento del evento en salud identificado. El registro de esta atencin corresponde a una consulta de valoracin integral y deteccin temprana con un diagnstico relacionado.

          p(numero="R" titulo="Ruta de Promocin y Mantenimiento de la Salud") Verificar o canalizar a atenciones en el marco de la RPMS. En caso de tener algn hallazgo o en caso de tener alguna condicin asociada a mayor riesgo, verificar la atencin por el profesional de pediatra. Importante verificar que al nio o nia se le haya realizado el tamizaje auditivo neonatal, en caso contrario ordenarlo para su realizacin en los primeros tres meses de vida, verificar resultados de los dems tamizajes. Importante verificar atencin para la promocin y apoyo a la lactancia materna en el primer mes. Recordar desparasitacin intestinal, fortificacin casera con micronutrientes (ciclo cada 6 meses  nios y nias entre 6 y 23 meses de edad), suplementacin con hierro y vitamina A (24 a 59 meses de edad). Suplementacin con hierro en lactantes con riesgo y prueba de hemoglobina, si se identifica riesgo. Vacunacin de acuerdo a esquema. Tener presente la valoracin y atenciones de salud bucal. Aclarar cuando se proyecta la siguiente consulta.

          p(numero="V" titulo="Vigilancia en Salud") Realizar la notificacin obligatoria de los eventos de inters en salud pblica identificados durante la consulta.

          p.mb-0(numero="I" titulo="Informacin en Salud") #[a(:href="obtenerLink('/downloads/plan_cuidado_ninos.pdf')" target="_blank") Anexo. Tips para informacion en salud primera infancia e infancia]              
          

          p(numero="R" titulo="Remitir a intervenciones colectivas  ") Del Plan de Intervenciones Colectivas de acuerdo con oferta territorial y entornos en los que se desarrolla.

          p(numero="E" titulo="Educacin para la Salud") incluye la canalizacin a escenario de educacin grupal para la salud (capacidades para el cuidado de la salud, la crianza promotora del desarrollo, alimentacin y nutricin; as como identificacin de signos de alarma). En caso de situacin o condicin especfica pueden programarse sesiones de educacin para la salud individuales.    

