                th.text-center(colspan='4',style="font-weight: 900; font-size: 19px; color:#fff; background-color:#f579a8;") Insumos y materiales para examen fsico peditrico:
              tr
                td(colspan='4',style="text-align: start; font-weight: normal; font-size: 16px;background-color:#f6f6f6") Fonendoscopio, equipo de rganos, tensimetro peditrico, cinta mtrica, pulso-oxmetro, optotipos morfoscpicos o angulares para agudeza visual etc.; balanza pesa beb y balanza de pie, infantmetro y tallmetro. Tablas de percentiles de tensin arterial para nios y para nias. Carn de salud infantil.

    #t_2_2.titulo-segundo
      .h2 2.2 Infancia

    p.mb-4 La poblacin sujeta que abarca las infancias son nios y nias desde los 6 aos hasta los 11 aos, 11 meses y 29 das. Entre muchos procesos, durante este periodo se presenta lo que se conoce como la latencia del crecimiento; que as mismo se acompaa de procesos cognitivos donde se ganan y perfeccionan capacidades, habilidades y destrezas que requieren de la esfera corporal; por lo cual se considera esencial el acompaamiento que estas transiciones implican.
    p.mb-5 La valoracin integral de la salud en la infancia debe ser realizada de forma complementaria entre los profesionales de medicina general o familiar y de enfermera, cumpliendo con el esquema de intervenciones/atenciones en salud individuales, definido en el lineamiento tcnico y operativo de la RPMS.

    .row.mb-4.justify-content-center 
      .col-lg-7.align-self-center
        .cajon.color-acento-contenido.p-4.mb-4
          p #[strong Importante:] contar con la presencia del cuidador y/o acompaante del nio(a) durante la consulta.

    figure.mb-5
      img(src='@/assets/curso/tema2/img1b.svg', alt='poblacin sujeta')

    .h4.mb-4 Anamnesis infancia
    p.mb-5 Se recomienda hacer nfasis en:

    .row.justify-content-center.mb-4
      .col-lg-10.alig-self-center
        PasosA.color-acento-contenido.mb-5(tipo='n')
          .row
            //1
            .col-md-11.mb-4.mb-md-0
              p Antecedentes personales, mdicos, hospitalarios, transfusionales, farmacolgicos, exposicin al humo de tabaco, alrgicos, quirrgicos, consumo de sustancias psicoactivas, esquema de vacunacin, comportamiento y relaciones interpersonales, conducta suicida y consumo de sustancias psicoactivas. Antecedentes ginecolgicos en nias con menarca.

          .row
            //2
            .col-md-11.mb-4.mb-md-0
              p Antecedentes familiares: debe hacerse un especial nfasis en enfermedades hereditarias, antecedentes de salud mental de los padres, hermanas/os y otros familiares, conducta suicida, consumo de alcohol y otras sustancias psicoactivas; duelo o muerte de personas significativas, divorcio de los padres, problemas en las relaciones de los progenitores, alteraciones de las emociones o del comportamiento.

          .row
            //3
            .col-md-11.mb-4.mb-md-0
              p Desarrollo y aprendizaje: en cada consulta, los padres o acudientes del nio o nia, deben informar sobre el rendimiento escolar, la aptitud de aprendizaje, la actitud en el aula y la vida social. Prcticas de crianza y cuidado: formas de comunicacin con el nio(a), actividades para estimular el desarrollo y el juego, cmo se relaciona con su familia y con otros adultos y nios, establecimiento de lmites y disciplina para corregir, refuerzos positivos, exposicin a violencias, prevencin de accidentes, exposicin a vulneraciones de derechos como el trabajo infantil.

          .row
            //4
            .col-md-11.mb-4.mb-md-0
              p Consumo y hbitos alimentarios indagando la frecuencia, cantidad, forma de preparacin y tipo de alimentos (incluyendo consumo de azcar y sal), para lo cual puede ser til la indagacin de la alimentacin en las ltimas 24 horas, se debe indagar sobre problemas en el hbito de comer

          .row
            //5
            .col-md-11.mb-4.mb-md-0
              p Rutinas y Hbitos: indagar sobre sueo, juego, actividad fsica, uso de tiempo libre, hbito urinario e intestinal, exposicin a pantallas.

          .row
            //6
            .col-md-11.mb-4.mb-md-0
              p Valoracin o actualizacin de la conformacin y dinmica de la familia a travs del Familiograma, indagar sobre las capacidades y recursos personales y familiares que inciden en el cuidado de la salud familiar, evaluar el grado de satisfaccin de la funcionalidad familiar, a travs del #[strong APGAR familiar.]

          .row
            //7
            .col-md-11.mb-4.mb-md-0
              p Valoracin o actualizacin de las condiciones de vida, del contexto social y las redes de apoyo social y comunitarias de la familia, pertenencia social y cultural a travs de #[strong Ecomapa.]

          .row
            //8
            .col-md-11.mb-4.mb-md-0
              p Avance en los compromisos acordados en sesiones de educacin individual previas e inquietudes del nio o nia o de los cuidadores. En caso de que existan inquietudes en relacin con el proceso de identidad de gnero, se debe ampliar la anamnesis para orientar adecuadamente o derivar.

    .h4.mb-4 Examen fsico completo en infancia 
    p.mb-4 Durante el procedimiento se debe realizar una anamnesis completa que incluya indagacin de antecedentes y revisin por sistemas. Se recomienda hacer nfasis en:



    .row.justify-content-center
      .col-lg-12.align-self-center
        .tarjeta-avatar-b.mb-4
          img(src='@/assets/curso/tema2/img6.svg' alt='AvatarTop')
          .tarjeta.tarjeta--azul
            .p-4
              .h3 Signos vitales
              p.mb-2 Examen fsico completo, partiendo de tomar todos los signos vitales. La tensin arterial debe tomarse con el manguito adecuado y debe establecerse su normalidad a travs del uso de la tabla de percentiles de tensin arterial en nios y nias.
    
    .row.justify-content-center
      .col-lg-12.align-self-center
        .tarjeta-avatar-b.mb-4
          img(src='@/assets/curso/tema2/img7.svg' alt='AvatarTop')
          .tarjeta.tarjeta--azul
            .p-4
              .h3 Valoracin del desarrollo
              p.mb-2 Evaluar el desarrollo de los nios en infancia a travs de la indagacin del desempeo del rendimiento escolar, del aprendizaje y del comportamiento de acuerdo a la revisin por sistemas. En caso de sospecha de dficit cognitivo se debe aplicar el test de la figura humana de Goodenough Harris en la misma consulta.

    .row.justify-content-center
      .col-lg-12.align-self-center
        .tarjeta-avatar-b.mb-4
          img(src='@/assets/curso/tema2/img8.svg' alt='AvatarTop')
          .tarjeta.tarjeta--azul
            .p-4
              .h3 Valoracin del estado nutricional y seguimiento a los parmetros antropomtricos
              p.mb-2 Se realizar tomando las medidas de peso y talla, para el clculo de los ndices relacionados con los mismos (IMC/edad y talla/edad). La clasificacin antropomtrica se realizar con base en lo establecido en la normatividad vigente  y la informacin debe ser registrada en las grficas de los patrones de crecimiento OMS para nias, nios y adolescentes menores de 18 aos

    .row.justify-content-center
      .col-lg-12.align-self-center
        .tarjeta-avatar-b.mb-4
          img(src='@/assets/curso/tema2/img9.svg' alt='AvatarTop')
          .tarjeta.tarjeta--azul
            .p-4
              .h3 Valoracin de la salud sexual  maduracin sexual
              p.mb-2 Evaluar la evolucin de los estadios de Tanner y realizar la exploracin de la regin ano-genital para descartar signos de violencia sexual as como anormalidades anatmicas.

    .row.justify-content-center
      .col-lg-12.align-self-center
        .tarjeta-avatar-b.mb-4
          img(src='@/assets/curso/tema2/img10.svg' alt='AvatarTop')
          .tarjeta.tarjeta--azul
            .p-4
              .h3 Valoracin de la salud visual
              p.mb-2 Realizar examen oftalmoscopio donde se debe explorar y evaluar las estructuras del segmento anterior y posterior, se debe valorar la agudeza visual usando optotipos morfoscpicos o angulares.

    .row.justify-content-center
      .col-lg-12.align-self-center
        .tarjeta-avatar-b.mb-4
          img(src='@/assets/curso/tema2/img11.svg' alt='AvatarTop')
          .tarjeta.tarjeta--azul
            .p-4
              .h3 Valoracin de la salud auditiva y comunicativa
              p.mb-2 Evaluacin del estado del odo y su integridad a travs de la inspeccin visual, la otoscopia, y de las funciones de la articulacin, voz, habla y desempeo comunicativo. Se recomienda aplicar la #[strong lista de chequeo de factores de riesgo de las enfermedades del odo, alteraciones auditivas, vestibulares y de la comunicacin] as mismo se debe aplicar el cuestionario #[strong VALE] durante todas las consultas.

    .row.justify-content-center
      .col-lg-12.align-self-center
        .tarjeta-avatar-b.mb-4
          img(src='@/assets/curso/tema2/img12.svg' alt='AvatarTop')
          .tarjeta.tarjeta--azul
            .p-4
              .h3 Valoracin de la salud bucal
              p.mb-2 Dirigida a realizar el reconocimiento del estado de las estructuras dentomaxilofaciales y de su funcionalidad.

    .row.justify-content-center
      .col-lg-12.align-self-center
        .tarjeta-avatar-b.mb-5
          img(src='@/assets/curso/tema2/img13.svg' alt='AvatarTop')
          .tarjeta.tarjeta--azul
            .p-4
              .h3 Valoracin de la salud mental
              p.mb-2 En este marco es importante indagar acerca de la calidad de la relacin de los padres hacia los hijos, dado que este factor es importante como protector o riesgo de posibles problemas y /o trastornos mentales.  Tener en cuenta los hallazgos a partir de la anamnesis, identificar la presencia de riesgos psicosociales para violencias y de exposicin a violencias, que puede incluir una o diversas formas de violencias de gnero, interpersonales o con ocurrencia en el conflicto armado, identificar lesiones por consumo de SPA, bajo rendimiento escolar, problemas de aprendizaje o de comportamiento, problemas en el hbito de comer, duelo o prdida significativa, as como convulsiones o ausencias. Si identifica algn riesgo de problema o trastorno mental aplicar el instrumento de tamizaje #[strong Reporting Questionnaire for Children (RQC).]

    h4.mb-5 Plan de cuidado infancia 

    .row.justify-content-center.mb-5
      .col-md-10.mb-5.mb-md-0
        LineaTiempoD.color-acento-contenido

          p(numero="S" titulo="Servicios sociales y atenciones a la familia") Atenciones complementarias de educacin, cultura, sector social  prosperidad social, entre otras. Verificar escolaridad. Definir atenciones a la familia. 

          p(numero="E" titulo="Enfermedad") Diagnstico, tratamiento y derivacin. Si de acuerdo con los hallazgos el mdico identifica una patologa, signo o sntoma que no ha sido diagnosticado anteriormente, en esta consulta se podr generar el diagnstico y establecer el manejo o tratamiento del evento en salud identificado. El registro de esta atencin corresponde a una consulta de valoracin integral y deteccin temprana con un diagnstico relacionado. 

          p(numero="R" titulo="Ruta de Promocin y Mantenimiento de la Salud") Verificar o canalizar a atenciones en el marco de la RPMS. Importante realizar tamizaje de anemia en nias una vez entre los 10 y los 13 aos; iniciar vacunacin con VPH (Virus del Papiloma Humano) a los 9 aos y Td (Toxoide Tetnico y Diftrico del adulto) a los 10 aos (segn antecedente vacunal). Tener presente la valoracin y atenciones de salud bucal. Aclarar cuando se proyecta la siguiente consulta. 
          
          p(numero="V" titulo="Vigilancia en Salud") Realizar la notificacin obligatoria de los eventos de inters en salud pblica identificados durante la consulta.  

          p.mb-0(numero="I" titulo="Informacin en Salud") #[a(:href="obtenerLink('/downloads/plan_cuidado_ninos.pdf')" target="_blank") Anexo. Tips para informacion en salud primera infancia e infancia]  

          p(numero="R" titulo="Remitir a intervenciones colectivas del Plan de Intervenciones Colectivas") De acuerdo a oferta territorial y entornos en los que se desarrolla.

          p(numero="E" titulo="Educacin para la salud") Incluye la canalizacin a un escenario de educacin grupal para la salud (capacidades de cuidado para promover la salud y el desarrollo, alimentacin y nutricin, capacidades socioemocionales y afrontamiento de sucesos vitales). En caso de situacin o condicin especfica pueden programarse sesiones de educacin para la salud individuales.
          

    .h4.mb-4 Instrumentos, insumos y dispositivos infancia

    p.mb-4 Cumplimiento de estndares de habilitacin para la consulta de medicina general, medicina familiar o enfermera. <br>Instrumentos:

    .row.mb-4.justify-content-center
      .col-lg-12.align-self-center
        .tabla-b.acento-contenido-contenido.mb-5 
          .tabla-b__header
            .h4.text-white.mb-0 Obligatorios
          table
            //caption Modelos gateway y agregadorg
            tbody.text-center
              tr(style="background-color:#f6f6f6;")
                th.text-start(style="font-weight: normal; font-size: 16px;") Tablas y grficas de los patrones de referencia para clasificacin nutricional 
                th.text-center(style="font-weight: normal; font-size: 16px;") Cuestionario VALE 
                th.text-center(style="font-weight: normal; font-size: 16px;") APGAR familiar
              tr
                th.text-center(colspan='4',style="font-weight: 900; font-size: 19px; color:#fff; background-color:#f579a8") Opcionales:
              tr
                td(colspan='4',style="font-weight: normal; font-size: 16px;background-color:#f6f6f6")
                  .row
                    .col-auto
                      ul.lista-ul
                        li
                          i.fas.fa-check
                          |	Cuestionario RQC (tamizaje de alteraciones de salud mental)
                        li
                          i.fas.fa-check
                          | Test de Estadios Tanner
                        li
                          i.fas.fa-check
                          | Lista de chequeo de factores de riesgo de las enfermedades del odo, alteraciones auditivas, vestibulares y de la comunicacin.
                        li
                          i.fas.fa-check
                          | Test de la figura humana de Goodenough Harris para dficit cognitivo.
                        li
                          i.fas.fa-check
                          | Familiograma
              tr
                th.text-center(colspan='4',style="font-weight: 900; font-size: 19px; color:#fff; background-color:#f579a8;") Insumos y materiales para examen fsico peditrico:
              tr
                td.text-start(colspan='4',style="font-weight: normal; font-size: 16px;background-color:#f6f6f6") Fonendoscopio equipo de rganos, tensimetro peditrico, cinta mtrica, pulso-oxmetro, optotipos morfoscpicos o angulares para agudeza visual, bscula y tallmetro etc. Carn de salud infantil y Tablas de percentiles de tensin arterial para nios y para nias.





    #t_2_3.titulo-segundo
      .h2 2.3 Adolescencia

    p.mb-4 La poblacin sujeto son adolescentes entre los 12 aos hasta los 17 aos, 11 meses y 29 das. La adolescencia es un momento de la vida al que es necesario aproximarse comprendiendo que involucra distintas fases. En este momento de vida, los individuos experimentan un proceso de maduracin y desarrollo a nivel biolgico, social y psicoemocional importante, por lo cual, la valoracin integral en salud es fundamental.  Aspectos como su salud mental, su sexualidad y la preparacin para una adecuada vivencia de su salud reproductiva son cruciales, enfatizando en la orientacin para el adecuado desarrollo de su identidad, autonoma y autoestima, promoviendo el desarrollo de capacidades para la toma de decisiones, que les permita tener una vida con propsito y desempear un rol importante en la sociedad.

    figure.mb-5
      img(src='@/assets/curso/tema2/img1c.svg', alt='poblacin sujeta')

    p.mb-5 Esta consulta debe ser realizada de forma complementaria entre los profesionales de medicina general o familiar y de enfermera. Importante contar con capacidades tcnicas e individuales para acompaar el proceso de desarrollo de los adolescentes; as como habilidades de comunicacin efectiva para abordar situaciones frecuentes en este momento vital (ejercicio de sus derechos sexuales y reproductivos, sospecha de embarazo, anticoncepcin, violencias, consumo de alcohol o tabaco, presin de pares, capacidad de liderazgo, participacin juvenil, entre otras). En el caso de adolescentes que consultan con sus padres o cuidadores, se debe informar que la parte inicial de la consulta se desarrolla preferiblemente a solas con el adolescente en reconocimiento de su autonoma. Es conveniente que el padre o la madre estn presentes durante el examen fsico, si as lo desea el adolescente. Las opiniones del padre o la madre y las interacciones entre ellos y el adolescente permitirn complementar la informacin sobre su desarrollo evolutivo, la comunicacin y conflictos familiares, as como apoyar recomendaciones sobre el cuidado de la salud.

    .row.mb-4.justify-content-center 
      .col-lg-10.align-self-center
        .cajon.color-acento-contenido.p-4.mb-4
          p #[strong Importante:] este procedimiento debe realizarse en un ambiente que garantice privacidad, libre de prejuicios y no discriminatorio, los datos suministrados pueden provenir del adolescente, su madre o padre, o de la interaccin entre ambos, por lo cual se requiere prestar suficiente atencin a todas las fuentes.

    .h4.mb-4 Anamnesis adolescencia
    p.mb-5 Se recomienda hacer nfasis en: 

    .row.justify-content-center.mb-4
      .col-lg-10.alig-self-center
        PasosA.color-acento-contenido.mb-5(tipo='n')
          .row
            //1
            .col-md-11.mb-4.mb-md-0
              p #[strong Antecedentes personales], incluyendo el desarrollo puberal. 
              p Verificar pruebas de tamizaje para anemia y resultados. Importante gestionar previamente para contar con resultado en la consulta


          .row
            //2
            .col-md-11.mb-4.mb-md-0
              p #[strong Antecedentes familiares], especialmente alteraciones hereditarias, as como trastornos o problemas de salud mental y/o consumo de SPA (sustancias psicoactivas) o alcohol propio o en la familia.

          .row
            //3
            .col-md-11.mb-4.mb-md-0
              p #[strong Aprendizaje:] asistencia escolar y desempeo acadmico; indagando desercin o riesgo, repitencia o desempeo bajo que puede deberse a problemas de aprendizaje no detectados previamente. Comportamiento y relaciones interpersonales. Prcticas de crianza de los cuidadores (incluyendo prcticas correctivas y riesgo de violencias).

          .row
            //4
            .col-md-11.mb-4.mb-md-0
              p Consumo y hbitos alimentarios, especialmente patrn de comidas, tipo de alimentos y dietas. Indagar factores de riesgo o conductas asociadas a trastornos de la conducta alimentaria.

          .row
            //5
            .col-md-11.mb-4.mb-md-0
              p #[strong Rutinas y hbitos]: sueo, actividad fsica, uso de tiempo libre, actividad fsica (recomendacin para adolescentes, 60 minutos diarios de actividad moderada o vigorosa, 5 das a la semana).

          .row
            //6
            .col-md-11.mb-4.mb-md-0
              p Valoracin de la dinmica familiar. #[strong APGAR familiar - Familiograma.]

          .row
            //7
            .col-md-11.mb-4.mb-md-0
              p Condiciones de vida, del contexto social y las redes de apoyo. #[strong Ecomapa]

          .row
            //8
            .col-md-11.mb-4.mb-md-0
              p Avance en los compromisos acordados en sesiones de educacin individual previas. Preocupaciones de salud y cuidado extra-escolar. 
          
          .row
            //9
            .col-md-11.mb-4.mb-md-0
              p #[strong Reconocimiento y ejercicio de Derechos Sexuales y Reproductivos y vivencia de la sexualidad.] Indagacin respecto a inicio de relaciones sexuales; as como proceso de orientacin sexual e identidad de gnero y vivencia del mismo. Conocimiento del proceso de sexualidad y reproduccin. #[strong Instrumento de valoracin de los derechos sexuales y derechos reproductivos.]

    .h4.mb-4 Examen fsico completo en la adolescencia 

    .row.justify-content-center
      .col-lg-12.align-self-center
        .tarjeta-avatar-b.mb-4
          img(src='@/assets/curso/tema2/img6.svg' alt='AvatarTop')
          .tarjeta.tarjeta--azul
            .p-4
              .h3 Signos vitales y examen fsico completo
    
    .row.justify-content-center
      .col-lg-12.align-self-center
        .tarjeta-avatar-b.mb-4
          img(src='@/assets/curso/tema2/img7.svg' alt='AvatarTop')
          .tarjeta.tarjeta--azul
            .p-4
              .h3 Valoracin del desarrollo
              p.mb-2 Valorar funciones cognitivas mantenindose atento durante la consulta en relacin a la identificacin de propsito en sus actividades, autocontrol, coherencia en el comportamiento, capacidad de resolver problemas, control interno, proyeccin a futuro, entre otras funciones ejecutivas. As mismo valorar la identidad y la autonoma (Instrumento de valoracin de la identidad e instrumento de valoracin de la autonoma).

    .row.justify-content-center
      .col-lg-12.align-self-center
        .tarjeta-avatar-b.mb-4
          img(src='@/assets/curso/tema2/img8.svg' alt='AvatarTop')
          .tarjeta.tarjeta--azul
            .p-4
              .h3 Valoracin del estado nutricional y seguimiento a los parmetros antropomtricos
              p.mb-2 A travs de medidas de peso y talla, para el clculo de los ndices relacionados con los mismos (IMC/edad y talla/edad) y a partir de all realizar la clasificacin antropomtrica de acuerdo a normatividad vigente.

    .row.justify-content-center
      .col-lg-12.align-self-center
        .tarjeta-avatar-b.mb-4
          img(src='@/assets/curso/tema2/img9.svg' alt='AvatarTop')
          .tarjeta.tarjeta--azul
            .p-4
              .h3 Valoracin de la salud sexual y reproductiva
              p.mb-2 Realizar la valoracin clnica de la maduracin sexual a travs del seguimiento a la evolucin de los estadios de Tanner (puede entregarse un espejo al adolescente para que el examen fsico se convierta en una experiencia que permita brindar explicacin y a su vez observar el examen de sus genitales). Correlacionar la maduracin sexual con el crecimiento fsico para correlacionar el proceso puberal con el sexo y la edad. Acompaar y remitir para asesora en procesos de identidad de gnero y orientacin sexual diversa.

    .row.justify-content-center
      .col-lg-12.align-self-center
        .tarjeta-avatar-b.mb-4
          img(src='@/assets/curso/tema2/img10.svg' alt='AvatarTop')
          .tarjeta.tarjeta--azul
            .p-4
              .h3 Valoracin de la salud visual
              p.mb-2 A partir del examen oftalmoscpico se debe explorar y evaluar las estructuras del segmento anterior y posterior, tales como anexos oculares, conjuntiva, crnea, esclera anterior, iris, cristalino, cmara anterior, vtreo, retina, cabeza del nervio ptico, estructuras vasculares retinales y coroides entre otras. Indagar acerca de preocupaciones sobre problemas visuales.

    .row.justify-content-center
      .col-lg-12.align-self-center
        .tarjeta-avatar-b.mb-4
          img(src='@/assets/curso/tema2/img11.svg' alt='AvatarTop')
          .tarjeta.tarjeta--azul
            .p-4
              .h3 Valoracin de la salud auditiva y comunicativa
              p.mb-2 Incluye la evaluacin del estado del odo y su integridad a travs de la inspeccin visual, la otoscopia, y de las funciones de la articulacin, voz, habla y desempeo comunicativo.

    .row.justify-content-center
      .col-lg-12.align-self-center
        .tarjeta-avatar-b.mb-4
          img(src='@/assets/curso/tema2/img12.svg' alt='AvatarTop')
          .tarjeta.tarjeta--azul
            .p-4
              .h3 Valoracin de la salud bucal
              p.mb-2 Realizar el reconocimiento del estado de las estructuras dentomaxilofaciales (cara, maxilar y mandbula, labios y comisura labial, mejillas-carrillos, enca, zona retro molar, piso de boca, superficie ventral y dorsal de lengua, paladar duro y blando, oro-faringe, articulacin temporomandibular (ATM) y estructuras dentales) y de su funcionalidad en los procesos de masticacin, deglucin, habla, fonacin, socializacin, afecto y autoestima, entre otras.

    .row.justify-content-center
      .col-lg-12.align-self-center
        .tarjeta-avatar-b.mb-5
          img(src='@/assets/curso/tema2/img13.svg' alt='AvatarTop')
          .tarjeta.tarjeta--azul
            .p-4
              .h3 Valoracin de la salud mental
              p.mb-2 Adicional a los hallazgos a partir de la anamnesis, se debe valorar la presencia de lesiones fsicas por exposicin a situaciones de violencia intrafamiliar, violencia sexual y de gnero o por violencia de pares (matoneo), lesiones por consumo de SPA, lesiones autoinflingidas. Si identifica algn riesgo en salud mental se debe aplicar el instrumento de tamizaje Reporting Questionnaire for Children (RQC) para los adolescentes entre 12 y 15 aos y Self Report Questionnaire (SRQ) a partir de los 16 aos. Ante el consumo de SPA se debe aplicar el instrumento Substance Involvement Screening Test (ASSIST) y en el de alcohol el instrumento Alcohol Use Disorders Identification Test (AUDIT).

    h4.mb-5 Plan de cuidado adolescencia 

    .row.justify-content-center.mb-5
      .col-md-10.mb-5.mb-md-0
        LineaTiempoD.color-acento-contenido

          p.mb-0(numero="S" titulo="Servicios sociales y atenciones a la familia") Atenciones complementarias de educacin en adolescentes desescolarizados, apoyo alimentario, transferencias monetarias condicionadas, prevencin y atencin del trabajo infantil, oferta de ocio y recreacin, entre otros. Definir atenciones a la familia.

          p.mb-0(numero="E" titulo="Enfermedad") Diagnstico, tratamiento y derivacin. Si de acuerdo con los hallazgos el mdico identifica una patologa, signo o sntoma que no ha sido diagnosticado anteriormente, en esta consulta se podr generar el diagnstico y establecer el manejo o tratamiento del evento en salud identificado. El registro de esta atencin corresponde a una consulta de valoracin integral y deteccin temprana con un diagnstico relacionado.

          p.mb-0(numero="R" titulo="Ruta de Promocin y Mantenimiento de la Salud") Verificar o canalizar atenciones en el marco de la RPMS (en caso dado atenciones de la RIAMP). Importante realizar tamizaje de anemia en nias una vez entre los 10 y los 13 aos y luego una vez entre los 14 y los 17 aos. Vacunacin con VPH (Vacuna Papiloma Humano) nias entre 12 a 17 aos y Td (Toxoide Tetnico y Diftrico del adulto) segn antecedente vacunal. Consulta de anticoncepcin cuando el adolescente presenta prcticas sexuales coitales sin uso de anticoncepcin o tienen intencin o riesgo de inicio de relaciones sexuales. De preferencia esta consulta debe realizarse de forma inmediata a este procedimiento (Nota: menores de 14 aos). Verificar tamizajes en caso de relaciones sexuales sin proteccin para VIH/sida y Sfilis. Test de embarazo en caso de sospecha. Tener presente la valoracin y atenciones de salud bucal. Aclarar para cuando se proyecta la siguiente consulta. 

          p.mb-0(numero="V" titulo="Vigilancia en Salud") Realizar la notificacin obligatoria de los eventos de inters en salud pblica identificados durante la consulta

          p.mb-0(numero="I" titulo="Informacin en Salud") #[a(:href="obtenerLink('/downloads/plan_cuidado_adolescentes.pdf')" target="_blank") Anexo. Tips de informacin en salud en adolescentes y jvenes] 

          p.mb-0(numero="R" titulo="Remitir a intervenciones colectivas del Plan de Intervenciones Colectivas") De acuerdo con oferta territorial y entornos en los que se desarrolla.

          p.mb-0(numero="E" titulo="Educacin para la salud") incluye la canalizacin a escenario de educacin grupal para la salud (capacidades de cuidado de la salud, alimentacin y nutricin, ejercicio de los derechos sexuales y derechos reproductivos, cesacin de tabaco en adolescentes con tabaquismo o exposicin al humo del tabaco, habilidades para la vida, habilidades socioemocionales para la promocin de la salud mental, establecimiento de relaciones sanas y constructivas y el afrontamiento de sucesos vitales). En caso de situacin o condicin especfica pueden programarse sesiones de educacin para la salud individuales (dificultades de afrontamiento, con dificultades para toma de decisiones, sin o con poco apoyo familiar, preocupaciones por su imagen corporal o peso, entre otras).  Considerar en estos ciclos, incorporar sesiones de trabajo con las familias o ubicar atenciones dirigidas a la familia especficamente, de acuerdo a los hallazgos.                
           

