    #t_2_4.titulo-segundo
      .h2 2.4 Juventud

    p.mb-4 #[strong La poblacin sujeto son] jvenes de 18 a 28 aos, 11 meses y 29 das.

    p.mb-4 En la juventud el individuo consolida tanto su desarrollo biolgico como su proyecto de vida, se realiza una transicin relacionada con la mayora de edad que implica la vinculacin al mercado laboral o la continuacin de estudios de formacin tcnica o profesional. Cobran mayor relevancia la formacin de estilos de vida que definirn el riesgo de desarrollar enfermedades no transmisibles en momentos posteriores de su vida. El riesgo de consumo abusivo de alcohol y sustancias psicoactivas se incrementa. Tambin es una etapa en la que se inicia la conformacin de la nueva familia.

    figure.mb-5
      img(src='@/assets/curso/tema2/img1d.svg', alt='poblacin sujeta')

    p.mb-5 Esta atencin se realizar a travs de una consulta por profesionales en medicina general o medicina familiar de acuerdo con lo contemplado en el esquema de intervenciones/atenciones en salud individuales, definido en el lineamiento tcnico y operativo de la RPMS. Es muy importante para la valoracin en este momento de curso de vida, contar con capacidades para valorar integralmente y apoyar el ejercicio de los derechos sexuales y reproductivos, sospecha de embarazo, anticoncepcin, ITS-VIH-SIDA, diversas formas de violencia, salud mental, prevencin del suicidio, prevencin del consumo de alcohol, prevencin del consumo de tabaco y cesacin del consumo de tabaco y drogas, aspectos bioticos y legales en la atencin de joven, manejo para la intervencin de grupos especficos en situacin de vulnerabilidad (diversidad sexual, discapacidad, indgenas, migrantes, infractores de ley), donde el abordaje articulado a otros sectores y otras ofertas es esencial y donde el trabajo comunitario  colectivo propicia el fortalecimiento del liderazgo y participacin juvenil, entre otros.

    .h4.mb-4 Anamnesis juventud
    p.mb-5 Se recomienda hacer nfasis en: 

    .row.justify-content-center.mb-4
      .col-lg-10.alig-self-center
        PasosA.color-acento-contenido.mb-5(tipo='n')
          .row
            //1
            .col-md-11.mb-4.mb-md-0
              p Verificar la realizacin y los resultados de las pruebas de tamizaje para deteccin temprana de riesgo cardiovascular y de cncer de cuello uterino. Gestionar previamente para contar con el resultado.


          .row
            //2
            .col-md-11.mb-4.mb-md-0
              p Antecedentes familiares, especialmente alteraciones hereditarias, as como trastornos o problemas de salud mental y consumo de SPA y alcohol propio y en la familia y exposicin a violencias.

          .row
            //3
            .col-md-11.mb-4.mb-md-0
              p Desarrollo, aprendizaje y vida laboral: desempeo acadmico, rastrear antecedente de trabajo infantil y/o adolescente. Tipo de labor, carga laboral actual, exposicin a riesgos.

          .row
            //4
            .col-md-11.mb-4.mb-md-0
              p Consumo y hbitos alimentarios, especialmente patrn de comidas, tipo de alimentos y dietas.

          .row
            //5
            .col-md-11.mb-4.mb-md-0
              p Rutinas y hbitos: sueo, actividad fsica, uso de tiempo libre, actividad fsica, manejo del estrs y situaciones de alta tensin, prctica frecuente del autoexamen de seno y autoexamen de testculo.

          .row
            //6
            .col-md-11.mb-4.mb-md-0
              p Valoracin de la dinmica familiar. #[strong APGAR familiar - Familiograma.]

          .row
            //7
            .col-md-11.mb-4.mb-md-0
              p Condiciones de vida, del contexto social y las redes de apoyo. #[strong Ecomapa]

          .row
