          p.mb-0(numero="E" titulo="Enfermedad") Diagnstico, tratamiento y derivacin. Si de acuerdo con los hallazgos el mdico identifica una patologa, signo o sntoma que no ha sido diagnosticado anteriormente, en esta consulta se podr generar el diagnstico y establecer el manejo o tratamiento del evento en salud identificado. El registro de esta atencin corresponde a una consulta de valoracin integral y deteccin temprana con un diagnstico relacionado.

          p.mb-0(numero="R" titulo="Ruta de Promocin y Mantenimiento de la Salud") Verificar o canalizar atenciones en el marco de la RPMS (en caso dado atenciones de la RIAMP). Ofrecer consulta preconcepcional y de orientacin para la anticoncepcin, de preferencia esta consulta debe realizarse de forma inmediata a este procedimiento. Tener presenta la valoracin de salud bucal. Verificar vacunacin en mujeres, Td (Toxoide Tetnico y Diftrico del adulto), segn antecedente vacunal. Tamizaje de cuello uterino (citologa) . Paraclnicos para tamizaje de riesgo cardiovascular si se identifica riesgo : glicemia basal, perfil lipdico, creatinina, uroanlisis segn clasificacin de riesgo. Tamizajes en caso de relaciones sexuales sin proteccin (VIH/sida, Sfilis, hepatitis B). Tamizaje para Hepatitis C, a las personas que recibieron transfusiones de sangre antes de 1996 en Colombia. Aclarar para cuando se proyecta la siguiente consulta.

