    #t_2_5.titulo-segundo
      .h2 2.5 Adultez

    p.mb-4 #[strong La poblacin sujeto son ] adultos de 29 a 59 aos, 11 meses y 29 das
    p.mb-4 Se configura en un proceso dinmico, en donde contina el desarrollo y las personas son capaces de establecer metas y poner en marcha los recursos necesarios para alcanzarlas, configurando sus trayectorias evolutivas personales y aprovechando las oportunidades disponibles en sus contextos. En este sentido, es importante subrayar que los patrones de desarrollo adulto no se configuran en algo novedoso, sino en la consolidacin de los aprendizajes previos y un perfilamiento cada vez ms claro de la individualidad. Por lo tanto, las intervenciones en este momento de vida permiten consolidar los aprendizajes y fortalecer las capacidades aprendidas para posibilitar el crecimiento y el logro de los objetivos planeados por cada individuo.

    figure.mb-5
      img(src='@/assets/curso/tema2/img1e.svg', alt='poblacin sujeta')

    p.mb-5 La valoracin integral se realizar a travs de una consulta por profesionales en medicina general o medicina familiar de acuerdo con lo contemplado en el esquema de intervenciones/atenciones en salud individuales definido en el lineamiento tcnico y operativo de la Ruta de Promocin y Mantenimiento de la Salud (RPMS). Se requiere contar con capacidades tcnicas e individuales, con un enfoque de curso de vida, que permita reconocer las caractersticas y potencialidades de la adultez, los efectos acumulativos para la salud a lo largo de la vida, la influencia de los sucesos vitales y la importancia de las vidas interconectadas en el desarrollo.

    .h4.mb-4 Anamnesis adultez
    p.mb-5 Se recomienda hacer nfasis en: 

    .row.justify-content-center.mb-4
      .col-lg-10.alig-self-center
        PasosA.color-acento-contenido.mb-5(tipo='n')
          .row
            //1
            .col-md-11.mb-4.mb-md-0
              p Verificar la realizacin y los resultados de las pruebas de tamizaje para deteccin temprana de riesgo cardiovascular, cncer de cuello uterino, cncer de mama, cncer de prstata y de colon y recto. Gestionar previamente para contar con el resultado.


          .row
            //2
            .col-md-11.mb-4.mb-md-0
              p Antecedentes familiares, enfermedades especialmente en familiares cercanos que sean heredables como hipertensin arterial, diabetes mellitus, dislipidemias, enfermedad coronaria, cncer, cerebrovasculares, alergias, asma, tuberculosis, gota, hemofilia, glaucoma; as como enfermedades degenerativas o genticas, trastornos mentales y/o consumo de SPA y/o alcohol propio o en la familia y exposicin a violencias.

          .row
            //3
            .col-md-11.mb-4.mb-md-0
              p Desarrollo y vida laboral: tipo de labor, carga laboral actual, exposicin a riesgos.

          .row
            //4
            .col-md-11.mb-4.mb-md-0
              p Consumo y hbitos alimentarios, especialmente patrn de comidas, tipo de alimentos y dietas.

          .row
            //5
            .col-md-11.mb-4.mb-md-0
              p Rutinas y hbitos: sueo, actividad fsica, uso de tiempo libre, actividad fsica. manejo del estrs y situaciones de alta tensin, prctica frecuente del autoexamen de seno y autoexamen de testculo. Valorar exposicin al humo de lea o carbn, consumo de tabaco (Fumador o exfumador: N de paquetes/ao = Nmero de cigarrillos diarios x nmero de aos / 20). En caso de identificar factores de riesgo para EPOC se debe aplicar cuestionario.

          .row
            //6
            .col-md-11.mb-4.mb-md-0
              p Valoracin de la dinmica familiar. #[strong APGAR familiar - Familiograma.]

          .row
            //7
            .col-md-11.mb-4.mb-md-0
              p Condiciones de vida, del contexto social y las redes de apoyo. #[strong Ecomapa]

          .row
            //8
            .col-md-11.mb-4.mb-md-0
              p Avance en los compromisos acordados en sesiones de educacin individual previas. Preocupaciones de salud. 
          
          .row
            //9
            .col-md-11.mb-4.mb-md-0
              p #[strong Reconocimiento y ejercicio de Derechos Sexuales y Reproductivos y vivencia de la sexualidad] en sus diferentes componentes (reproductivo, afectivo, ertico y comunicativo-relacional).

    .h4.mb-4 Examen fsico completo en la adultez  

    .row.justify-content-center
      .col-lg-12.align-self-center
        .tarjeta-avatar-b.mb-4
          img(src='@/assets/curso/tema2/img6.svg' alt='AvatarTop')
          .tarjeta.tarjeta--azul
            .p-4
              .h3 Signos vitales y examen fsico completo
              p.mb-2 Incluyendo la valoracin de las mamas (inspeccionar simetra, contorno o forma de la misma, el color, los pezones y la areola); areola);identificar curvaturas anormales en trax; as como identificar lesiones o alteraciones de la piel, heridas o hematomas que permitan identificar posibles traumas o lesiones por exposicin al sol tipo queratosis actnicas, la cual es relevante buscarlas por ser premalignas y alteraciones en la sensibilidad (hipoestesia o anestesia), las cuales son caractersticas de enfermedad de Hansen. En el caso de que la persona se encuentre con limitaciones de movilidad deben ser evaluadas las reas de presin. Realizar pruebas para revisar insuficiencia venosa en miembros inferiores y la funcionalidad de las articulaciones.
    
    .row.justify-content-center
      .col-lg-12.align-self-center
        .tarjeta-avatar-b.mb-4
          img(src='@/assets/curso/tema2/img7.svg' alt='AvatarTop')
          .tarjeta.tarjeta--azul
            .p-4
              .h3 Valoracin del estado nutricional y seguimiento a los parmetros antropomtricos
              p.mb-2 Adems, identificar y detectar casos de ingesta excesiva o deficiente de caloras o nutrientes a partir de la alimentacin.

    .row.justify-content-center
      .col-lg-12.align-self-center
        .tarjeta-avatar-b.mb-4
          img(src='@/assets/curso/tema2/img8.svg' alt='AvatarTop')
          .tarjeta.tarjeta--azul
            .p-4
              .h3 Valoracin del riesgo cardiovascular y metablico
              p.mb-2 Los instrumentos que permiten esto se encuentran condensados en la aplicacin Conoce tu riesgo En caso de identificar riesgo cardiovascular medio, alto, muy alto o riesgo moderado y alto de diabetes, deber ordenar los siguientes exmenes como insumo para realizar la gestin de riesgo respectiva: glucemia basal, perfil lipdico (colesterol de alta densidad HDL, colesterol de baja densidad LDL, colesterol total triglicridos), creatinina y uroanlisis; segn los resultados de los exmenes se debe derivar a la ruta integral de atencin en salud para la poblacin con riesgo o presencia de alteraciones cardio cerebro vascular metablica.

    .row.justify-content-center
      .col-lg-12.align-self-center
        .tarjeta-avatar-b.mb-4
          img(src='@/assets/curso/tema2/img9.svg' alt='AvatarTop')
          .tarjeta.tarjeta--azul
            .p-4
              .h3 Valoracin de la salud sexual y reproductiva
              p.mb-2 Identificar cambios fsicos y psicolgicos propios del momento que afectan la participacin o la satisfaccin en las actividades sexuales (signos y sntomas relacionados con la funcionalidad sexual, orientacin sexual, deseo, excitacin, lubricacin, orgasmo, satisfaccin, ereccin, eyaculacin, dolor). Evaluar las condiciones mdicas que afectan la salud sexual incluyendo la depresin, la enfermedad cardaca, enfermedad cerebrovascular, EPOC y diabetes. Mujeres con intencin reproductiva o en gestacin, deben derivarse a la Ruta Materno Perinatal.

    .row.justify-content-center
      .col-lg-12.align-self-center
        .tarjeta-avatar-b.mb-4
          img(src='@/assets/curso/tema2/img10.svg' alt='AvatarTop')
          .tarjeta.tarjeta--azul
            .p-4
              .h3 Valoracin de la salud visual
              p.mb-2 Se puede desarrollar mediante cuatro tcnicas: oftalmoscopia a distancia, directa, e indirecta y la biomicroscopia con lentes accesorias. Si la agudeza visual se encuentra reducida por debajo de 20/50, debe derivarse a una consulta resolutiva por optometra para confirmar o descartar un componente refractivo

    .row.justify-content-center
      .col-lg-12.align-self-center
        .tarjeta-avatar-b.mb-4
          img(src='@/assets/curso/tema2/img11.svg' alt='AvatarTop')
          .tarjeta.tarjeta--azul
            .p-4
              .h3 Valoracin de la salud auditiva y comunicativa
              p.mb-2 Incluye la evaluacin del estado del odo y su integridad. Pueden incorporarse dos preguntas de percepcin: Tiene usted o ha tenido algn problema en el odo? y Cree usted que escucha bien?.

    .row.justify-content-center
      .col-lg-12.align-self-center
        .tarjeta-avatar-b.mb-4
          img(src='@/assets/curso/tema2/img12.svg' alt='AvatarTop')
          .tarjeta.tarjeta--azul
            .p-4
              .h3 Valoracin de la salud bucal
              p.mb-2 Realizar el reconocimiento del estado de las estructuras dentomaxilofaciales y de su funcionalidad en los procesos de masticacin, deglucin, habla, fonacin, socializacin, afecto y autoestima, entre otras.

    .row.justify-content-center
      .col-lg-12.align-self-center
        .tarjeta-avatar-b.mb-5
          img(src='@/assets/curso/tema2/img13.svg' alt='AvatarTop')
          .tarjeta.tarjeta--azul
            .p-4
              .h3 Valoracin de la salud mental
              p.mb-2 Incluye el examen mental (pensamiento  lgica, contenido y velocidad, lenguaje  coherencia, fluidez-, afecto y estado de nimo, percepciones, sensorio  orientacin, concentracin, memoria, clculos, razonamiento abstracto-, introspeccin y juicio); y la valoracin de las estrategias de afrontamiento frente a sucesos vitales (normativos y no normativos) con el fin de prevenir alteraciones en la salud e identificar factores de riesgo para trastornos de comportamiento, problemas y trastornos mentales. Apoyarse para la deteccin de alteraciones en los instrumentos disponibles: SRQ - Cuestionario de auto reporte (personas desde los 16 aos para detectar alteraciones de salud mental); #[strong Preguntas Whooley] (en mayores de 18 aos para detectar depresin); #[strong GAD-2]  Escala para trastorno de ansiedad generalizado (mayores de 18 aos). En caso de sospecha de consumo de sustancias psicoactivas y tabaco #[strong (ASSIST)]; o en caso de sospecha de consumo problemtico de alcohol #[strong (AUDIT)]. En caso que la persona asuma un rol de cuidador y exista la sospecha de sobrecarga del cuidador #[strong (Escala de Zarit)]  as mismo considerar en caso de cuidadores de personas con dependencia funcional.


    h4.mb-5 Plan de cuidado adultez 

    .row.justify-content-center.mb-5
      .col-md-10.mb-5.mb-md-0
        LineaTiempoD.color-acento-contenido

          p.mb-0(numero="S" titulo="Servicios sociales y atenciones a la familia") Atenciones complementarias de cultura, deporte, recreacin, educacin, bsqueda de empleo, programas de apoyo alimentario, atencin a vctimas, entre otras. Definir atenciones a la familia. 

          p.mb-0(numero="E" titulo="Enfermedad") si de acuerdo con los hallazgos el mdico identifica una patologa, signo o sntoma que no ha sido diagnosticado anteriormente, en esta consulta se podr generar el diagnstico y establecer el manejo o tratamiento del evento en salud identificado. El registro de esta atencin corresponde a una consulta de valoracin integral y deteccin temprana con un diagnstico relacionado.

          p.mb-0(numero="R" titulo="Ruta de Promocin y Mantenimiento de la Salud") verificar o canalizar atenciones en el marco de la RPMS (en caso dado atenciones de la RIAMP). Ofrecer consulta preconcepcional y de orientacin para la anticoncepcin, de preferencia esta consulta debe realizarse de forma inmediata a este procedimiento. Tener presente la valoracin y atenciones de salud bucal. Verificar vacunacin en mujeres, Td (Toxoide Tetnico y Diftrico del adulto), segn antecedente vacunal, tamizaje para deteccin temprana de riesgo cardiovascular, cncer de cuello uterino, cncer de mama, cncer de prstata y de colon y recto. Tamizaje de riesgo cardiovascular cada 5 aos: glicemia basal, perfil lipdico (colesterol de alta densidad HDL, colesterol de baja densidad LDL, colesterol total triglicridos), creatinina y uroanlisis. Tamizajes en caso de relaciones sexuales sin proteccin (VIH/sida, Sfilis, hepatitis B). Tamizaje para Hepatitis C, a las personas que recibieron transfusiones de sangre antes de 1996 en Colombia. Aclarar para cuando se proyecta siguiente la consulta.

          p.mb-0(numero="V" titulo="Vigilancia en Salud") realizar la notificacin obligatoria de los eventos de inters en salud pblica identificados durante la consulta

          p.mb-0(numero="I" titulo="Informacin en Salud") #[a(:href="obtenerLink('/downloads/plan_cuidado_adultos.pdf')" target="_blank") Anexo. Tips de informacin en salud para adultos]  

          p.mb-0(numero="R" titulo="Remitir a intervenciones colectivas del Plan de Intervenciones Colectivas") De acuerdo con oferta territorial y entornos en los que se desarrolla.

          p.mb-0(numero="E" titulo="Educacin para la salud") Incluye la canalizacin a escenario de educacin grupal para la salud (capacidades para el cuidado de la salud, alimentacin y nutricin, ejercicio de los derechos sexuales y derechos reproductivos, habilidades socioemocionales para la promocin de la salud mental, establecimiento de relaciones sanas y constructivas y afrontamiento de sucesos vitales). En caso de situacin o condicin especfica pueden programarse sesiones de educacin para la salud individuales, (dificultades de afrontamiento, con dificultades para toma de decisiones, sin o con poco apoyo familiar, tabaquismo, entre otras).  
           

    .h4.mb-4 Instrumentos, insumos y dispositivos en la adultez

    p.mb-4 Cumplimiento de estndares de habilitacin para la consulta de medicina general, medicina familiar o enfermera. <br>Instrumentos:

    .row.mb-4.justify-content-center
      .col-lg-12.align-self-center
        .tabla-b.acento-contenido-contenido.mb-5 
          .tabla-b__header
            .h4.text-white.mb-0 Obligatorios
