    #t_2_6.titulo-segundo
      .h2 2.6 Vejez



    p.mb-4 La poblacin sujeto son personas de 60 aos o ms. Existen diversos enfoques para comprender el proceso de envejecimiento y las caractersticas de la vejez; sin embargo, estos pueden vivirse desde las prdidas o desde la plenitud, de acuerdo con los efectos acumulados a lo largo de la vida, la confluencia de las oportunidades y los recursos, tanto individuales como generacionales, que afectan el continuo de la vida de cada persona, as como su condicin y posicin social y su conjugacin con la edad, el gnero, la clase social y el origen tnico, que representan ciertas diferencias en el acceso y disfrute de dichos recursos y oportunidades. La valoracin integral en la vejez agrupa un conjunto de atenciones coordinadas, complementarias y efectivas para determinar el estado de salud de la persona adulta mayor, detectar tempranamente la exposicin a riesgos y alteraciones para su prevencin y/o derivacin para manejo oportuno de salud requerido, y a partir de los hallazgos potenciar las capacidades, habilidades y prcticas de cuidado de la salud, la promocin del envejecimiento activo y la construccin concertada del plan de cuidado de la persona adulta mayor.


    figure.mb-5
      img(src='@/assets/curso/tema2/img1f.svg', alt=' La poblacin sujeto son personas de 60 aos o ms')

    p.mb-5 La consulta de valoracin integral de la persona adulta mayor debe ser realizada por profesionales en medicina general o medicina familiar con conocimientos y experiencia en gerontologa y geriatra. Importante favorecer el envejecimiento activo como proceso de optimizacin de las oportunidades en salud, participacin y seguridad de las personas adultas mayores; el mantenimiento de la funcionalidad y la calidad de vida de las personas adultas mayores como principales indicadores de salud y bienestar. prevencin de diversas formas de violencia, uso de tecnologas que inciden en la prevencin de la dependencia y la fragilidad; estrategias que impulsen la solidaridad, la insercin social y el intercambio intergeneracional en el marco de las acciones dirigidas a la poblacin adulta mayor.

    .h4.mb-4 Anamnesis vejez
    p.mb-5 Se recomienda hacer nfasis en: 

    .row.justify-content-center.mb-4
      .col-lg-10.alig-self-center
        PasosA.color-acento-contenido.mb-5(tipo='n')
          .row
            //1
            .col-md-11.mb-4.mb-md-0
              p #[strong Antecedentes personales], indagar factores de riesgo o presencia de enfermedades metablicas, cardiovasculares, pulmonares (EPOC), tuberculosis, cncer, alteraciones sensoriales, neurolgicas, msculo esquelticas, mentales o del comportamiento, manejo del estrs, enfermedades transmisibles, infecciones de trasmisin sexual, urolgicas, exposicin a riesgos psicosociales para violencias, abandono, enfermedades o accidentes laborales, exposicin a factores de riesgo derivados del trabajo.


          .row
            //2
            .col-md-11.mb-4.mb-md-0
              p #[strong Antecedentes familiares], enfermedades que presenten o hayan presentado familiares cercanos por la posibilidad que sean heredables, trastornos o problemas mentales y/o consumo de SPA o alcohol propio o en la familia, conducta suicida, consumo de alcohol y otras sustancias psicoactivas, exposicin a violencias.

          .row
            //3
            .col-md-11.mb-4.mb-md-0
              p.mb-5 #[strong  Presencia de sndromes geritricos, ] un diagnstico adecuado permite identificar riesgos relacionados y definir planes de tratamiento tempranos y prcticos que inciden en la disminucin de la morbilidad, discapacidad y mortalidad evitables de las personas adultas mayores:
              .row.justify-content-center.mb-4
                .col-lg-11.align-self-center
                  ol.lista-ol--cuadro
                    li 
                      .lista-ol--cuadro__vineta
                        span a
                      p
                        strong Deterioro cognoscitivo: 
                        | se caracteriza por la prdida o reduccin temporal o permanente de las funciones mentales superiores (lenguaje, razonamiento, clculo, memoria, praxias, gnosias, etc.), siendo el ms comn la prdida de la memoria; por lo cual debe indagarse al respecto.
                      
                    li 
                      .lista-ol--cuadro__vineta
                        span b
                      p
                        strong Inmovilidad: 
                        | restriccin involuntaria en la capacidad de transferencia y desplazamiento, generalmente debido a dificultades fsicas, funcionales y psicosociales. Es necesario indagar en antecedentes o presencia de enfermedades osteoarticulares como la artrosis, osteoporosis, fracturas, debilidad muscular, lesiones en los pies, patologas cardiovasculares y trastornos neuropsiquitricos, entre otros, factores de riesgo que limiten la movilidad o el desplazamiento de la persona.

                    li 
                      .lista-ol--cuadro__vineta
                        span c
                      p
                        strong Inestabilidad y cadas: 
                        | es el riesgo de precipitaciones repentinas al suelo de manera involuntaria, que en algunas ocasiones pueden estar acompaadas con prdida de la conciencia. Evaluar presencia de sarcopenia o debilidad, alteraciones de la marcha, limitaciones de la movilidad, dficit visual, deterioro cognoscitivo, diabetes mellitus tipo II, hipotensin ortosttica, consumo de medicamentos con efecto sedante, dependencia funcional, y factores del entorno (tapetes, escaleras sin pasamanos, espacios con baja visibilidad entre otros). Asimismo, preguntar antecedentes de cadas anteriores y analizar los hallazgos de valoracin integral.
                    li 
                      .lista-ol--cuadro__vineta
                        span d
                      p
                        strong Fragilidad: 
                        | es un incremento de la vulnerabilidad a estresores de baja intensidad. Debe evaluarse: fuerza de presin y velocidad de la marcha, prdida de peso, ausencia de energa y poca actividad fsica; y un fenotipo multidominio basado en un dficit en salud (enfermedades, discapacidades, anormalidades radiogrficas, electrocardiografas o de laboratorio).
                    li 
                      .lista-ol--cuadro__vineta
                        span e
                      p #[strong Incontinencia de esfnteres:] adems de su significado mdico, tiene un profundo impacto en el bienestar de los adultos mayores, comprometiendo la autoestima y la dignidad de los mismos. Indagar infecciones, alteraciones psicolgicas, delirios o estados confusionales, tratamientos farmacolgicos, lesiones medulares, debilidad de suelo plvico, entre otras causas. 
                      
                      
                    li 
                      .lista-ol--cuadro__vineta
                        span f
                      p
                        strong Depresin: 
                        | en el adulto mayor se presenta con algunos sntomas que son infrecuentes en personas jvenes: deterioro cognitivo que simula una demencia, ansiedad, insomnio y somatizacin. Importante indagar acerca de la prdida de peso, irritabilidad, insomnio, cambios del estado de nimo, somatizacin, prdida del apetito, hipoacusia y aislamiento social. As mismo, indagar respecto al consumo de SPA o alcohol que puede estar asociado a afectaciones del estado de nimo en este momento de curso de vida.
                    li 
                      .lista-ol--cuadro__vineta
                        span g
                      p
                        strong Iatrogenia: 
                        | la Iatrogenia medicamentosa se ha convertido en uno de los principales problemas de las personas mayores. Esto por dos razones bsicas: porque el envejecimiento reduce los ndices teraputicos y tambin porque los pacientes geritricos tienden a usar un nmero mayor de medicamentos, favoreciendo la interaccin medicamentosa. Por consiguiente, en cada consulta es necesario analizar los medicamentos y dosis prescritas, as como los automedicados, los naturistas y homeopticos; identificar posibles interacciones medicamentosas y frmaco-enfermedad.

          .row
            //4
            .col-md-11.mb-4.mb-md-0
              p #[strong Consumo y hbitos alimentarios], evaluar la regularidad en el patrn de comidas, el tipo de alimentos consumidos, as como dietas que puedan contribuir al desequilibrio nutricional que permitan identificar la ingesta excesiva o deficiente de caloras o nutrientes a partir de la alimentacin.

          .row
            //5
            .col-md-11.mb-4.mb-md-0
              p #[strong Derechos Sexuales y Reproductivos y la vivencia de la sexualidad] en sus componentes (reproductivo, afectivo, ertico y comunicativo- relacional) en condiciones de libertad, equidad y dignidad. Indagar: orientacin sexual, identidad de gnero.

          .row
            //6
            .col-md-11.mb-4.mb-md-0
              p #[strong Hbitos y rutinas:] actividad fsica, higiene oral y corporal (incluyendo lavado de manos), descanso y sueo, hbito intestinal y urinario, foto proteccin, prevencin de accidentes, uso del tiempo libre y ocio.

          .row
            //7
            .col-md-11.mb-4.mb-md-0
              p #[strong Valoracin o actualizacin de la conformacin y dinmica de la familia] a travs del Familiograma y el APGAR familiar, a fin de comprender las condiciones de la familia.

          .row
            //8
            .col-md-11.mb-4.mb-md-0
              p #[strong Valoracin o actualizacin de las condiciones de vida], del contexto social y las redes de apoyo social y comunitarias de la familia (Ecomapa).
          
          .row
            //9
            .col-md-11.mb-4.mb-md-0
              p #[strong Actividad laboral o econmica ]actual y previa. Indagar sobre el tipo de labor, tipo de vinculacin laboral y la antigedad de la misma, la carga laboral.

          .row
            //10
            .col-md-11.mb-4.mb-md-0
              p #[strong Compromisos acordados en sesiones de educacin individual previas] y avances e inquietudes o preocupaciones.

    .h4.mb-4 Examen fsico completo en la vejez 

    .row.justify-content-center
      .col-lg-12.align-self-center
        .tarjeta-avatar-b.mb-4
          img(src='@/assets/curso/tema2/img6.svg' alt='AvatarTop')
          .tarjeta.tarjeta--azul
            .p-4
              .h3 Signos vitales y examen fsico completo
              p.mb-2 Inspeccionar aspecto general para reconoces cambio en estado de nimo y cuidado general que puede asociarse a depresin, abandono u otra situacin. En el caso de que la persona se encuentre con limitaciones de movilidad deben ser evaluadas las reas de presin.
    
    .row.justify-content-center
      .col-lg-12.align-self-center
        .tarjeta-avatar-b.mb-4
          img(src='@/assets/curso/tema2/img7.svg' alt='AvatarTop')
          .tarjeta.tarjeta--azul
            .p-4
              .h3 Valoracin de capacidad funcional
              p.mb-2 Valorar fuerza, sensibilidad, reflejos normales y patolgicos, equilibrio, coordinacin y temblor. Evaluar la marcha en el cambio de posicin de sentado a de pie, desplazndose al menos 3 metros y luego al darse la vuelta y caminar de nuevo y finalmente al volver a sentarse (lo cual permite adems detectar riesgo de cadas recurrentes). Para valorar independencia y funcionalidad utilizar los instrumentos definidos: ndice de Barthel (independencia en actividades bsicas de la vida diaria), escala de Lawton-Brody (nivel de dependencia en actividades instrumentales de la vida diaria) y test de Linda Fried.(deteccin temprana de personas adultas mayores con pre-fragilidad y fragilidad).

    .row.justify-content-center
      .col-lg-12.align-self-center
        .tarjeta-avatar-b.mb-4
          img(src='@/assets/curso/tema2/img8.svg' alt='AvatarTop')
          .tarjeta.tarjeta--azul
            .p-4
              .h3 Valoracin del estado nutricional y seguimiento a los parmetros antropomtricos
              p.mb-2 Adems, identificar y detectar casos de ingesta excesiva o deficiente de caloras o nutrientes a partir de la alimentacin. 
      
    .row.justify-content-center
      .col-lg-12.align-self-center
        .tarjeta-avatar-b.mb-4
          img(src='@/assets/curso/tema2/img14.svg' alt='AvatarTop')
          .tarjeta.tarjeta--azul
            .p-4
              .h3 Valoracin del riesgo cardiovascular y metablico
              p.mb-2 Los instrumentos que permiten esto se encuentran condensados en la aplicacin Conoce tu riesgo. 

    .row.justify-content-center
      .col-lg-12.align-self-center
        .tarjeta-avatar-b.mb-4
          img(src='@/assets/curso/tema2/img9.svg' alt='AvatarTop')
          .tarjeta.tarjeta--azul
            .p-4
              .h3 Valoracin de la salud sexual y reproductiva
              p.mb-2 Identificar cambios fsicos y psicolgicos propios del momento que afectan la participacin o la satisfaccin en las actividades sexuales. Evaluar las condiciones mdicas que afectan la salud sexual incluyendo la depresin, la enfermedad cardaca, enfermedad cerebrovascular, EPOC y diabetes.

    .row.justify-content-center
      .col-lg-12.align-self-center
        .tarjeta-avatar-b.mb-4
          img(src='@/assets/curso/tema2/img10.svg' alt='AvatarTop')
          .tarjeta.tarjeta--azul
            .p-4
              .h3 Valoracin de la salud visual
              p.mb-2 Se puede desarrollar mediante cuatro tcnicas: oftalmoscopia a distancia, directa, e indirecta y la biomicroscopia con lentes accesorias. Si la agudeza visual se encuentra reducida por debajo de 20/50, debe derivarse a una consulta resolutiva por optometra para confirmar o descartar un componente refractivo.

    .row.justify-content-center
