          .tarjeta.tarjeta--azul
            .p-4
              .h3 Valoracin de la salud auditiva y comunicativa
              p.mb-2 Incluye la evaluacin del estado del odo y su integridad. Pueden incorporarse dos preguntas de percepcin: Tiene usted o ha tenido algn problema en el odo? y Cree usted que escucha bien?. Tener en cuenta si la persona es usuaria de ayudas audiolgicas o no; antes de evaluar la comprensin del lenguaje y el desempeo comunicativo y las funciones de articulacin, voz y habla.

    .row.justify-content-center
      .col-lg-12.align-self-center
        .tarjeta-avatar-b.mb-4
          img(src='@/assets/curso/tema2/img12.svg' alt='AvatarTop')
          .tarjeta.tarjeta--azul
            .p-4
              .h3 Valoracin de la salud bucal
              p.mb-2 Realizar el reconocimiento del estado de las estructuras dentomaxilofaciales y de su funcionalidad en los procesos de masticacin, deglucin, habla, fonacin, socializacin, afecto y autoestima, entre otras.

    .row.justify-content-center
      .col-lg-12.align-self-center
        .tarjeta-avatar-b.mb-5
          img(src='@/assets/curso/tema2/img13.svg' alt='AvatarTop')
          .tarjeta.tarjeta--azul
            .p-4
              .h3 Valoracin de la salud mental
              p.mb-2 Incluye la valoracin de: apariencia y comportamiento (conducta espontnea, atuendo, apariencia, postura, expresiones, ademanes, acciones extraas, movimientos repetitivos, lentos o excesivos, postura anormal, contacto visual deficiente, expresiones faciales anormales, manierismos); lenguaje y comunicacin (disposicin e inters para comunicarse, flujo general del lenguaje; alteraciones del lenguaje; tono y contenido del discurso; relacin entre comunicacin verbal y no verbal); procesos de pensamiento (velocidad del habla, continuidad del habla, contenido del discurso, temas recurrentes, alteraciones del pensamiento (del curso y del contenido); estado de nimo y afecto (estado emocional predominante, estado emocional que acompaa el discurso, fluctuaciones en estado de nimo, correspondencia entre afecto y discurso); funcionamiento cognoscitivo (nivel de concentracin, atencin y estado de alerta; funcionamiento amnsico (memoria a corto y largo plazo, amnesia, hipermnesia), vocabulario, nivel de conocimientos de acuerdo con contexto personal y social); funcionamiento sensorial y motor (conciencia del entorno, orientacin en tiempo, espacio y persona; alteraciones sensoriales; coordinacin motora; dificultades motoras); adems de valorar estrategias de afrontamiento frente a sucesos vitales. Valorar riesgo de exposicin a violencias. Apoyarse para la deteccin de alteraciones en los instrumentos disponibles: Minimental State (en caso de sospecha de sugerentes de deterioro cognitivo), Preguntas Whooley (en mayores de 18 aos para detectar depresin); GAD-2  Escala para trastorno de ansiedad generalizado (mayores de 18 aos). En caso de sospecha de consumo de sustancias psicoactivas y tabaco (ASSIST); o en caso de sospecha de consumo problemtico de alcohol (AUDIT). En el caso de presentar dependencia, realizar valoracin de sobrecarga en el cuidador (Escala de Zarit).
    
    
    h4.mb-5 Plan de cuidado vejez 

