          p.mb-0(numero="E" titulo="Enfermedad") Diagnstico, tratamiento y derivacin. Si de acuerdo con los hallazgos el mdico identifica una patologa, signo o sntoma que no ha sido diagnosticado anteriormente, en esta consulta se podr generar el diagnstico y establecer el manejo o tratamiento del evento en salud identificado. El registro de esta atencin corresponde a una consulta de valoracin integral y deteccin temprana con un diagnstico relacionado.

          p.mb-0(numero="R" titulo="Ruta de Promocin y Mantenimiento de la Salud") Verificar o canalizar atenciones en el marco de la RPMS. Tener presente la valoracin y atenciones de salud bucal. Verificacin anual contra influenza. Tamizaje para cncer de cuello uterino, cncer de mama, cncer de prstata y de colon y recto. Tamizaje de riesgo cardiovascular y metablico cada 5 aos: glucemia basal, perfil lipdico (colesterol de alta densidad HDL, colesterol de baja densidad LDL, colesterol total triglicridos), creatinina y uroanlisis. Tamizajes en caso de relaciones sexuales sin proteccin (VIH/sida, Sfilis, hepatitis B). Tamizaje para Hepatitis C, a personas mayor de 50 aos una vez en la vida. Aclarar para cuando se proyecta la siguiente consulta.

          p.mb-0(numero="V" titulo="Vigilancia en Salud") Realizar la notificacin obligatoria de los eventos de inters en salud pblica identificados durante la consulta.

