          p.mb-0(numero="R" titulo="Remitir a intervenciones colectivas del Plan de Intervenciones Colectivas") De acuerdo a oferta territorial y entornos en los que se desarrolla.

          p.mb-0(numero="E" titulo="Educacin para la salud") Incluye la canalizacin a ciclo de entrenamiento cognitivo. En caso de situacin o condicin especfica pueden programarse sesiones de educacin para la salud individuales, (dificultades de afrontamiento, poco apoyo familiar o social, baja autoestima, experiencias negativas en relacin con la vivencia de la sexualidad asociado a cultura negativa del envejecimiento, compromiso de la movilidad o la autonoma, riesgo de desproteccin social, polifarmacia, alteraciones del sueo, tabaquismo, entre otras).                
            

